@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap");
.star--white {
  color: #ffffff;
}

.triangle-celeste {
  border-color: #00bdd5;
}

.topmenu {
  background-color: #1a1a1a;
  height: 35px;
}
.topmenu .topmenu__container {
  display: flex;
  width: 80%;
  height: 100%;
}
.topmenu .topmenu__container .topmenu__item {
  display: flex;
  display: flex;
  justify-content: flex-end;
  list-style: none;
  align-items: center;
  margin: 0px;
  padding: 0px;
  width: 100%;
}
.topmenu .topmenu__container .topmenu__item .topmenu__itemlink {
  display: flex;
  justify-content: center;
  margin: 0px 10px;
  font-size: 0.8em;
  height: 25px;
  align-items: center;
}
.topmenu .topmenu__container .topmenu__item .topmenu__itemlink a {
  color: #a9a9a9;
  text-decoration: none;
}

.primarymenu {
  background-color: #101000;
}
.primarymenu .primarymenu__container {
  display: flex;
  width: 80%;
}
.primarymenu .primarymenu__container .primarymenu__logo {
  width: 40%;
  margin-left: 20%;
}
.primarymenu .primarymenu__container .primarymenu__logo .primarymenu__logoimage {
  width: 60%;
  padding: 0.6em;
}
.primarymenu .primarymenu__container .primarymenu__item {
  display: flex;
  width: 60%;
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin: 0px;
  padding: 0px;
}
.primarymenu .primarymenu__container .primarymenu__item .primarymenu__itemlink {
  display: flex;
  justify-content: center;
  margin: 0px 10px;
  font-size: 1em;
  text-align: center;
  align-items: center;
}
.primarymenu .primarymenu__container .primarymenu__item .primarymenu__itemlink a {
  color: #ffffff;
  text-decoration: none;
}

.banner {
  position: relative;
  width: 100%;
}
.banner .banner__container {
  width: 100%;
}
.banner .banner__container .banner__image {
  width: 100%;
}
.banner .banner__container .banner__image .banner__imageitem {
  width: 100%;
}
.banner .banner__container .banner__item {
  display: flex;
  position: absolute;
  height: 40px;
  list-style: none;
  top: 45%;
  left: 25%;
  right: 25%;
}
.banner .banner__container .banner__item .banner__form {
  background-color: #ffffff;
  color: #a9a9a9;
  border-radius: 7px;
  border: 2px solid #ffdd00;
  width: 85%;
  height: 30px;
  margin-right: 15px;
  padding-left: 1em;
}
.banner .banner__container .banner__item .banner__itemlink {
  background-color: #ffdd00;
  color: #000000;
  width: 15%;
  border: 2px solid #ffdd00;
  border-radius: 5px;
  height: 35px;
  font-weight: bold;
  cursor: pointer;
}

.programs .programs__header {
  padding: 0px;
  background-color: #e7e7e7;
  cursor: pointer;
}
.programs .programs__header ul {
  display: flex;
  list-style: none;
  margin: 0% 0% 0% 15%;
  padding: 0px;
  padding: 5px 0px;
}
.programs .programs__header ul li a {
  font-style: italic;
  font-size: 0.8em;
  margin: 0px;
  text-decoration: none;
  color: #000000;
  margin: 5px;
}
.programs .programs__container {
  display: flex;
  margin: 0 300px;
}
.programs .programs__container .filter {
  width: 20%;
}
.programs .programs__container .filter .filter__title {
  margin-bottom: 30px;
}
.programs .programs__container .filter .filter__title h2 {
  font-size: 1.4em;
  padding: 10px 0px;
  border-bottom: 2px solid #ffdd00;
  margin-bottom: 0px;
}
.programs .programs__container .filter .filter__title p {
  font-size: 0.8em;
}
.programs .programs__container .filter .filter__selectors .selectors {
  list-style: none;
  padding: 0px;
}
.programs .programs__container .filter .filter__selectors .selectors .selectors__info {
  display: flex;
  justify-content: space-between;
  height: 30px;
  margin-bottom: 5px;
  border-bottom: 1px solid #a9a9a9;
  cursor: pointer;
}
.programs .programs__container .filter .filter__selectors .selectors .selectors__info span:first-child {
  font-size: 0.8em;
  padding: 5px 10px 10px;
  margin: 0px;
}
.programs .programs__container .filter .filter__selectors .selectors .selectors__info span:last-child {
  font-size: 1em;
  padding: 5px 10px 10px;
  margin: 0px;
  font-weight: bold;
}
.programs .programs__container .filter .filter__button {
  text-align: center;
}
.programs .programs__container .filter .filter__button .filter__buttonlink {
  background-color: #ffdd00;
  color: #000000;
  width: 60%;
  border: 2px solid #ffdd00;
  border-radius: 5px;
  height: 30px;
  font-weight: bold;
  cursor: pointer;
}
.programs .programs__container .cards {
  margin-left: 50px;
  width: 80%;
}
.programs .programs__container .cards .cards__selectors {
  display: flex;
  cursor: pointer;
}
.programs .programs__container .cards .cards__selectors span {
  margin: 0px 50px;
  padding: 35px 10px 10px;
  position: relative;
}
.programs .programs__container .cards .cards__selectors span:first-child {
  font-weight: bold;
  border-bottom: 2px solid #ffdd00;
}
.programs .programs__container .cards .cards__selectors span:first-child:before {
  content: "";
  width: 0px;
  height: 0px;
  border-left: 10px solid transparent;
  border-right: 9px solid transparent;
  border-top: 10px solid #ffdd00;
  position: absolute;
  bottom: -10px;
  right: 70px;
}
.programs .programs__container .cards .cards__targets {
  width: 100%;
  margin: 50px 0px;
}
.programs .programs__container .cards .cards__targets .targets {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0px;
  width: 100%;
}
.programs .programs__container .cards .cards__targets .targets .targets__container {
  background-color: #ffffff;
  width: 30%;
  margin: 1em;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__image {
  width: 100%;
  height: 170px;
  position: relative;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__image img:first-child {
  position: absolute;
  z-index: 0;
  width: 100%;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__image img:nth-child(2) {
  position: absolute;
  left: 67%;
  top: 41%;
  z-index: 1;
  width: 0;
  height: 0;
  border-right: 50px solid #ffdd00;
  border-top: 50px solid transparent;
  border-left: 50px solid transparent;
  border-bottom: 50px solid #ffdd00;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__image img:last-child {
  position: absolute;
  width: 13%;
  left: 85%;
  top: 72%;
  z-index: 2;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__info {
  margin: 0px 20px 20px;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__info .targets__title {
  font-size: 1.4em;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__information {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__information .information__icon {
  width: 20%;
  margin: 10px 0px;
  align-self: center;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__information .information__text {
  width: 80%;
  margin: 10px 0px;
  font-size: 0.8em;
  align-self: center;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__information .information__text p {
  margin: 5px 0px;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__button {
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__button button {
  width: 45%;
  height: 35px;
  margin: 20px 0px;
  border: 1px none #ffdd00;
  border-radius: 5px;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__button button a {
  text-decoration: none;
  color: #000000;
  font-weight: bold;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__button button:first-child {
  background-color: #cccccc;
}
.programs .programs__container .cards .cards__targets .targets .targets__container .targets__button button:last-child {
  background-color: #ffdd00;
}
.programs .programs__pagination {
  display: flex;
  justify-content: center;
  margin: 0px 300px 50px;
}
.programs .programs__pagination .pagination {
  display: flex;
  justify-content: center;
}
.programs .programs__pagination .pagination a {
  background-color: #ffffff;
  color: #000000;
  text-decoration: none;
  padding: 8px 16px;
  margin: 2px;
}
.programs .programs__pagination .pagination a:active {
  background-color: #ffdd00;
  color: #000000;
}
.programs .programs__pagination .pagination a:hover {
  background-color: #cccccc;
  color: #000000;
}

.footer__container {
  background-color: #000000;
}
.footer__container .footer__information {
  width: 80%;
  display: flex;
  margin: auto;
  justify-content: center;
}
.footer__container .footer__information .information__address {
  width: 15%;
  padding: 45px 0px;
}
.footer__container .footer__information .information__address .address__icon {
  position: relative;
}
.footer__container .footer__information .information__address .address__icon img {
  width: 60%;
  padding-bottom: 10%;
}
.footer__container .footer__information .information__address .address__icon::before {
  content: "";
  background-color: #ffdd00;
  height: 1px;
  width: 40%;
  top: 70px;
  position: absolute;
}
.footer__container .footer__information .information__address .address__info {
  margin-right: 40%;
}
.footer__container .footer__information .information__address .address__info ul {
  list-style: none;
  padding: 0px;
}
.footer__container .footer__information .information__address .address__info ul li {
  color: #ffffff;
  font-size: 0.8em;
  text-align: center;
}
.footer__container .footer__information .information__contact {
  width: 15%;
  padding: 45px 0px;
}
.footer__container .footer__information .information__contact .contact__icon img {
  width: 60%;
  padding-bottom: 10%;
}
.footer__container .footer__information .information__contact .contact__info {
  margin-right: 40%;
}
.footer__container .footer__information .information__contact .contact__info ul {
  list-style: none;
  padding: 0px;
}
.footer__container .footer__information .information__contact .contact__info ul li {
  color: #ffffff;
  font-size: 0.8em;
  text-align: center;
}
.footer__container .footer__information .information__normativity {
  width: 13%;
  padding: 45px 0px 45px 20px;
  border-left: 1px solid #222222;
}
.footer__container .footer__information .information__normativity .normativity__title {
  padding: 10% 20% 10% 0%;
  position: relative;
}
.footer__container .footer__information .information__normativity .normativity__title h4 {
  color: #ffffff;
  margin: 0px;
}
.footer__container .footer__information .information__normativity .normativity__title::before {
  content: "";
  background-color: #ffdd00;
  height: 1px;
  width: 40%;
  top: -5px;
  position: absolute;
}
.footer__container .footer__information .information__normativity .normativity__info ul {
  list-style: none;
  padding: 0px;
}
.footer__container .footer__information .information__normativity .normativity__info ul li {
  color: #818181;
  font-size: 0.8em;
  cursor: pointer;
}
.footer__container .footer__information .information__quicklinks {
  width: 13%;
  padding: 45px 20px 45px 0px;
  border-right: 1px solid #222222;
}
.footer__container .footer__information .information__quicklinks .quicklinks__title {
  padding: 10% 20% 10% 0%;
  position: relative;
}
.footer__container .footer__information .information__quicklinks .quicklinks__title h4 {
  color: #ffffff;
  margin: 0px;
}
.footer__container .footer__information .information__quicklinks .quicklinks__title::before {
  content: "";
  background-color: #ffdd00;
  height: 1px;
  width: 40%;
  top: -5px;
  position: absolute;
}
.footer__container .footer__information .information__quicklinks .quicklinks__info ul {
  list-style: none;
  padding: 0px;
}
.footer__container .footer__information .information__quicklinks .quicklinks__info ul li {
  color: #818181;
  font-size: 0.8em;
  cursor: pointer;
}
.footer__container .footer__information .information__socialnetworks {
  width: 13%;
  padding: 45px 20px;
}
.footer__container .footer__information .information__socialnetworks .socialnetworks__title {
  padding: 10% 20% 10% 0%;
  position: relative;
}
.footer__container .footer__information .information__socialnetworks .socialnetworks__title h4 {
  color: #ffffff;
  margin: 0px;
  font-size: 0.8em;
}
.footer__container .footer__information .information__socialnetworks .socialnetworks__title::before {
  content: "";
  background-color: #ffdd00;
  height: 1px;
  width: 40%;
  top: -5px;
  position: absolute;
}
.footer__container .footer__information .information__socialnetworks .socialnetworks__title::after {
  border-top: 2px solid #ffdd00;
}
.footer__container .footer__information .information__socialnetworks .socialnetworks__icon ul {
  list-style: none;
  padding: 0px;
  display: flex;
}
.footer__container .footer__information .information__socialnetworks .socialnetworks__icon ul li {
  cursor: pointer;
}
.footer__container .footer__information .information__socialnetworks .socialnetworks__icon ul li img {
  width: 75%;
}
.footer__container .footer__end {
  width: 80%;
  display: flex;
  margin: auto;
  padding: 3% 0% 2%;
}
.footer__container .footer__end .footer__endleft p {
  color: #818181;
  font-size: 0.8em;
}
.footer__container .footer__end .footer__endright {
  margin-left: 310px;
}
.footer__container .footer__end .footer__endright p {
  color: #818181;
  font-size: 0.8em;
}

body {
  margin: 0px;
  font-family: "Lato", sans-serif;
  background-color: #eeeeee;
}